}

@-webkit-keyframes blinkingText {
    0% {
        color: whitesmoke;
    }
    49% {
        color: transparent;
    }
    50% {
        color: transparent;
    }
    99% {
        color: transparent;
    }
    100% {
        color: whitesmoke;
    }
}

@keyframes blinkingText {
    0% {
        color: whitesmoke;
    }
    49% {
        color: transparent;
    }
    50% {
        color: transparent;
    }
    99% {
        color: transparent;
    }
    100% {
        color: whitesmoke;
    }
}

